body {
  margin: 1rem 10rem;
  padding: 0;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.header h2 {
  font-family: 'Roboto', sans-serif;
  color: #3c2946;
} 
.header button {
  margin: auto 0;
height: 20px;
color: white;
background-color: #2d4059;
cursor: pointer; 
}
.main {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  background-color: lightgrey;
  border-radius: 10px;
  box-shadow: 0 2px 2px;
  height: 155px;
}
.topic-card {
  margin: 10px;
  background-color: white;
  border-radius: 10px;
  height: 135px;
cursor: pointer; 
}

.topic-card img {
  width: 90%;
  height: 50px;
  padding: 10px 10px 0 10px;
  text-align: center;
}
.card-title {
  margin: 0;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}
.card-body {
  display: flex;
  justify-content: space-between;
  margin-top: 0;
  padding: 0 10px 5px 10px;
}

.module-card-title {
  padding: 1.5rem;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 20px;
  font-family: 'Montserrat', sans-serif;
}
.flash-card {
  margin: 10px;
  background-color: lightgrey;
  border: 1px solid black;
}